import { ReactElement, forwardRef, Ref } from 'react';

import { PlaylistCard } from '@/features/playlist/index.js';

import { CarouselBlock } from '@/shared/ui/index.js';

import { PersonalPlaylistsModel, PersonalPlaylistsDataItemModel } from '../../model/blocks/PersonalPlaylists.js';

interface Props extends PersonalPlaylistsModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

const PersonalPlaylistsComponent = ({ containerClassName, meta, data, forwardRef: ref }: Props): ReactElement => {
    return (
        <CarouselBlock
            containerClassName={containerClassName}
            ref={ref}
            isLoading={!data?.items || data?.items?.length === 0}
            title={meta.title}
            description={meta.description}
            viewAllActionLink={meta.viewAllActionLink}
        >
            {data?.items.map((item: PersonalPlaylistsDataItemModel) => {
                return (
                    <PlaylistCard
                        key={item.data.playlist.key}
                        playlist={item.data.playlist}
                        customDescription={item.data.description}
                        contentLinesCount={4}
                    />
                );
            })}
        </CarouselBlock>
    );
};

export const PersonalPlaylists = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <PersonalPlaylistsComponent forwardRef={ref} {...props} />
));
