import { ReactElement, forwardRef, Ref } from 'react';

import { ArtistCard } from '@/features/artist/index.js';

import { CarouselBlock } from '@/shared/ui/index.js';

import { CollectionArtistModel, CollectionArtistDataItemModel } from '../../model/blocks/CollectionArtists.js';

interface Props extends CollectionArtistModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

const CollectionArtistComponent = ({ containerClassName, meta, data, forwardRef: ref }: Props): ReactElement => {
    return (
        <CarouselBlock
            containerClassName={containerClassName}
            ref={ref}
            isLoading={!data?.items || data?.items?.length === 0}
            title={meta.title}
            description={meta.description}
            viewAllActionLink={meta.viewAllActionLink}
        >
            {data?.items.map((item: CollectionArtistDataItemModel) => {
                return <ArtistCard key={item.id} artist={item} contentLinesCount={3} />;
            })}
        </CarouselBlock>
    );
};

export const CollectionArtists = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <CollectionArtistComponent forwardRef={ref} {...props} />
));
