import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType } from '@yandex-music-int/resource-collection';

import { Artist, ArtistModel } from '@/entities/artist/index.js';

import { LandingBaseBlock } from './LandingBaseBlock.js';

const CollectionArtistData = types.model('CollectionArtistData', {
    items: types.array(Artist)
});

export const CollectionArtist = LandingBaseBlock.props({
    type: types.union(
        types.literal(LandingBlockType.COLLECTION_ARTISTS),
        types.literal(LandingBlockType.PERSONAL_ARTISTS),
        types.literal(LandingBlockType.NEW_STARS_ARTISTS),
        types.literal(LandingBlockType.EDITORIAL_ARTISTS)
    ),
    data: types.maybe(CollectionArtistData)
})
    .named('CollectionArtist')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }

            return self.data?.items?.length !== 0;
        }
    }));

export type CollectionArtistModel = Instance<typeof CollectionArtist>;
export type CollectionArtistDataModel = Instance<typeof CollectionArtistData>;
export type CollectionArtistDataItemModel = ArtistModel;
