import { cast } from 'mobx-state-tree';

import { NewRelease, NewReleasesResponse } from '@yandex-music-int/resource-collection';

import { NewReleasesDataModel } from '../../model/blocks/NewReleases.js';

import { landingAlbumResponseToModel } from './common/landingAlbumResponseToModel.js';

export const newReleasesResponseToModel = (response: NewReleasesResponse): NewReleasesDataModel => {
    const items = (response || {}).newReleases?.map((newRelease: NewRelease) => {
        const { album, artists, releaseDate } = newRelease;

        return {
            album: landingAlbumResponseToModel(album, artists),
            releaseDate
        };
    });

    return cast({
        items
    });
};
