import { ReactElement, forwardRef, Ref } from 'react';

import { PlaylistCard } from '@/features/playlist/index.js';

import { CarouselBlock } from '@/shared/ui/index.js';

import {
    CollectionLikedPlaylistsModel,
    CollectionLikedPlaylistsDataItemModel
} from '../../model/blocks/CollectionLikedPlaylists.js';

interface Props extends CollectionLikedPlaylistsModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

const CollectionLikedPlaylistsComponent = ({
    containerClassName,
    meta,
    data,
    forwardRef: ref
}: Props): ReactElement => {
    return (
        <CarouselBlock
            containerClassName={containerClassName}
            ref={ref}
            isLoading={!data?.items || data?.items?.length === 0}
            title={meta.title}
            description={meta.description}
            viewAllActionLink={meta.viewAllActionLink}
        >
            {data?.items.map((item: CollectionLikedPlaylistsDataItemModel) => {
                return <PlaylistCard key={item.key} playlist={item} contentLinesCount={3} />;
            })}
        </CarouselBlock>
    );
};

export const CollectionLikedPlaylists = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <CollectionLikedPlaylistsComponent forwardRef={ref} {...props} />
));
