import { ReactElement, forwardRef, Ref } from 'react';

import { LandingEntityType } from '@yandex-music-int/resource-collection';

import { AlbumCard } from '@/features/album/index.js';
import { ArtistCard } from '@/features/artist/index.js';
import { PlaylistCard } from '@/features/playlist/index.js';

import { CarouselBlock } from '@/shared/ui/index.js';

import { RecentlyPlayedDataItemModel, RecentlyPlayedModel } from '../../model/blocks/RecentlyPlayed.js';

interface Props extends RecentlyPlayedModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

const RecentlyPlayedComponent = ({ containerClassName, meta, data, forwardRef: ref }: Props): ReactElement => {
    return (
        <CarouselBlock
            containerClassName={containerClassName}
            ref={ref}
            isLoading={!data?.items || data?.items?.length === 0}
            title={meta.title}
            viewAllActionLink={meta.viewAllActionLink}
        >
            {data?.items.map((item: RecentlyPlayedDataItemModel) => {
                switch (item.type) {
                    case LandingEntityType.ALBUM:
                        return <AlbumCard key={item.data.id} album={item.data} contentLinesCount={3} />;

                    case LandingEntityType.ARTIST:
                        return <ArtistCard key={item.data.id} artist={item.data} contentLinesCount={3} />;

                    case LandingEntityType.PLAYLIST:
                        return <PlaylistCard key={item.data.uuid} playlist={item.data} contentLinesCount={3} />;
                }
            })}
        </CarouselBlock>
    );
};

export const RecentlyPlayed = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <RecentlyPlayedComponent forwardRef={ref} {...props} />
));
