import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, forwardRef, Ref, useId, useMemo, useRef } from 'react';

import { Carousel } from '@yandex-music-int/sol/components/Carousel';
import { TabPanel, useTabsState } from '@yandex-music-int/sol/components/Tabs';

import { AlbumCard, AlbumWithArtistsModel } from '@/features/album/index.js';

import carouselStyles from '@/shared/ui/CarouselBlock/CarouselBlock.module.scss';
import {
    BlockHeader,
    CarouselControls,
    CarouselShimmer,
    Tab,
    TabCarousel,
    TabCarouselShimmerWithCovers
} from '@/shared/ui/index.js';

import { InStyleDataItemModel, InStyleModel } from '../../../model/blocks/InStyle.js';

import styles from './InStyle.module.scss';

interface Props extends InStyleModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

export const InStyleComponent = observer(
    ({ containerClassName, meta, data, isLoading, forwardRef: ref }: Props): ReactElement => {
        const tabsState = useTabsState(0);
        const labelId = useId();
        const carouselRef = useRef<HTMLOListElement | null>(null);

        const tabPanels = useMemo(() => {
            if (isLoading) {
                return (
                    <Carousel
                        ref={carouselRef}
                        itemClassName={carouselStyles.item}
                        containerClassName={styles.content}
                        aria-labelledby={`${labelId} ${labelId}-description`}
                    >
                        {CarouselShimmer()}
                    </Carousel>
                );
            }
            return data?.items.map((item: InStyleDataItemModel) => {
                return (
                    <TabPanel name={item.tab.id} value={tabsState.value} key={item.tab.id}>
                        <Carousel
                            ref={carouselRef}
                            itemClassName={carouselStyles.item}
                            containerClassName={styles.content}
                            aria-labelledby={`${labelId} ${labelId}-description`}
                        >
                            {item.data.map((album: AlbumWithArtistsModel) => {
                                return <AlbumCard key={album.id} contentLinesCount={3} album={album} />;
                            })}
                        </Carousel>
                    </TabPanel>
                );
            });
        }, [isLoading, data?.items, labelId, tabsState.value]);

        return (
            <section className={styles.root} ref={ref}>
                <BlockHeader
                    className={clsx(styles.header, containerClassName)}
                    title={meta.title}
                    description={meta.description}
                    labeledForId={labelId}
                    viewAllActionLink={meta.viewAllActionLink}
                    controls={<CarouselControls className={styles.controls} carouselRef={carouselRef} />}
                />
                <TabCarousel
                    className={clsx(styles.tabCarousel, styles.important, containerClassName)}
                    isLoading={isLoading}
                    shimmer={
                        <TabCarouselShimmerWithCovers
                            className={clsx(styles.tabCarousel, styles.important, containerClassName)}
                        />
                    }
                    aria-labelledby={labelId}
                    {...tabsState}
                >
                    {data?.items.map(({ tab }: InStyleDataItemModel) => {
                        return (
                            <Tab
                                value={tab.id}
                                key={tab.id}
                                aria-label={tab.title}
                                title={tab.title}
                                covers={tab.covers}
                                className={clsx(styles.tab, styles.important)}
                                withCovers
                            />
                        );
                    })}
                </TabCarousel>
                {tabPanels}
            </section>
        );
    }
);

export const InStyle = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <InStyleComponent forwardRef={ref} {...props} />
));
