import { cast } from 'mobx-state-tree';

import { TabsResponse, Tab } from '@yandex-music-int/resource-collection';

import { TabDataModel } from '../../model/blocks/Tabs.js';

export const tabsResponseToModel = (response: TabsResponse): TabDataModel[] | undefined => {
    return (response || {}).tabs?.map((tab: Tab) => {
        return cast({
            id: tab.id,
            title: tab.title,
            subtitle: tab.subtitle,
            covers: tab.covers
        });
    });
};
