import { cast } from 'mobx-state-tree';

import { CollectionArtistsResponse, LandingArtistItem } from '@yandex-music-int/resource-collection';

import { CollectionArtistDataModel } from '../../model/blocks/CollectionArtists.js';

import { landingArtistResponseToModel } from './common/landingArtistResponseToModel.js';

export const collectionArtistsResponseToModel = (response: CollectionArtistsResponse): CollectionArtistDataModel => {
    const items = (response || {}).likedArtists?.map((likedArtist: LandingArtistItem) => {
        return landingArtistResponseToModel(likedArtist.data.artist);
    });

    return cast({
        items
    });
};
