'use client';

import { useEffect, useState } from 'react';

import { CONTAINER_CONFIG_TOKEN, useContainer, getTLDHost, useUrl, useLayout } from '@/shared/lib/index.js';

export const useWizardUrl = (): string => {
    const container = useContainer();
    const { tld, href } = useUrl();
    const { colorScheme } = useLayout();

    const config = container.get(CONTAINER_CONFIG_TOKEN);
    const oldWebHost = getTLDHost(config.oldWebHost, tld);

    const [wizardUrl, setWizardUrl] = useState('');

    useEffect(() => {
        setWizardUrl(
            `https://${oldWebHost}/wizard/?callback_url=${encodeURIComponent(href)}&forceTheme=${colorScheme}`
        );
    }, [colorScheme, href, oldWebHost]);

    return wizardUrl;
};
