import { Instance, types } from 'mobx-state-tree';

import { LoadingState } from '@/shared/lib/index.js';

export const LandingBlockMetaSource = types.model('LandingBlockMetaSource', {
    uri: types.string,
    countWeb: types.maybe(types.number),
    count: types.maybe(types.number)
});

export const LandingBlockMeta = types.model('LandingBlockMeta', {
    title: types.maybe(types.string),
    description: types.maybe(types.string),
    viewAllActionLink: types.maybeNull(types.string),
    source: types.maybe(LandingBlockMetaSource)
});

export const LandingBaseBlock = types
    .model('LandingBaseBlock', {
        loadingState: types.enumeration(Object.values(LoadingState)),
        id: types.string,
        meta: LandingBlockMeta
    })
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isNeededToLoad() {
            return self.loadingState === LoadingState.IDLE;
        },
        get isLoading() {
            return self.loadingState === LoadingState.PENDING;
        },
        get isRejected() {
            return self.loadingState === LoadingState.REJECT;
        }
    }));

export type LandingBlockMetaModel = Instance<typeof LandingBlockMeta>;
export type LandingBlockMetaSourceModel = Instance<typeof LandingBlockMetaSource>;
