import { Instance, types } from 'mobx-state-tree';

import { ChartTracks } from './blocks/ChartTracks.js';
import { CollectionAlbums } from './blocks/CollectionAlbums.js';
import { CollectionArtist } from './blocks/CollectionArtists.js';
import { CollectionCreatedPlaylists } from './blocks/CollectionCreatedPlaylists.js';
import { CollectionLikedPlaylists } from './blocks/CollectionLikedPlaylists.js';
import { InStyle } from './blocks/InStyle.js';
import { NewPlaylists } from './blocks/NewPlaylists.js';
import { NewReleases } from './blocks/NewReleases.js';
import { PersonalPlaylists } from './blocks/PersonalPlaylists.js';
import { PlaylistWithTracks } from './blocks/PlaylistWithTracks.js';
import { Promotions } from './blocks/Promotions.js';
import { RecentlyPlayed } from './blocks/RecentlyPlayed.js';
import { Special } from './blocks/Special.js';
import { Wizard } from './blocks/Wizard.js';

// У types.union максимальное количество аргументов 9
export const LandingBlock = types.union(
    types.union(
        ChartTracks,
        CollectionAlbums,
        CollectionArtist,
        CollectionLikedPlaylists,
        CollectionCreatedPlaylists,
        PlaylistWithTracks,
        NewReleases,
        NewPlaylists,
        Promotions
    ),
    types.union(RecentlyPlayed, InStyle, PersonalPlaylists, Special, Wizard)
);

export type LandingBlockModel = Instance<typeof LandingBlock>;
