import { cast } from 'mobx-state-tree';

import {
    LandingEntityType,
    LandingAlbumEntity,
    LandingArtistEntity,
    LandingPlaylistEntity,
    RecentlyPlayedItem,
    RecentlyPlayedResponse
} from '@yandex-music-int/resource-collection';

import {
    RecentlyPlayedDataAlbumItemModel,
    RecentlyPlayedDataArtistItemModel,
    RecentlyPlayedDataPlaylistItemModel,
    RecentlyPlayedDataModel,
    RecentlyPlayedDataItemModel
} from '../../model/blocks/RecentlyPlayed.js';

import { landingAlbumResponseToModel } from './common/landingAlbumResponseToModel.js';
import { landingArtistResponseToModel } from './common/landingArtistResponseToModel.js';
import { landingPlaylistResponseToModel } from './common/landingPlaylistResponseToModel.js';

const artistResponseToModel = (artist: LandingArtistEntity): RecentlyPlayedDataArtistItemModel => {
    return {
        type: LandingEntityType.ARTIST,
        data: landingArtistResponseToModel(artist)
    };
};

const albumResponseToModel = (
    album: LandingAlbumEntity,
    artists: LandingArtistEntity[]
): RecentlyPlayedDataAlbumItemModel => {
    return {
        type: LandingEntityType.ALBUM,
        data: landingAlbumResponseToModel(album, artists)
    };
};

const playlistResponseToModel = (playlist: LandingPlaylistEntity): RecentlyPlayedDataPlaylistItemModel => {
    return {
        type: LandingEntityType.PLAYLIST,
        data: landingPlaylistResponseToModel(playlist)
    };
};

export const recentlyPlayedResponseToModel = (response: RecentlyPlayedResponse): RecentlyPlayedDataModel => {
    const items = (response || {}).items?.map((item: RecentlyPlayedItem): RecentlyPlayedDataItemModel => {
        switch (item.type) {
            case LandingEntityType.PLAYLIST:
                return playlistResponseToModel(item.data.playlist);

            case LandingEntityType.ALBUM:
                return albumResponseToModel(item.data.album, item.data.artists);

            case LandingEntityType.ARTIST:
                return artistResponseToModel(item.data.artist);
        }
    });

    return cast({
        items
    });
};
