import { ReactElement, forwardRef, Ref } from 'react';

import { PlaylistCard } from '@/features/playlist/index.js';

import { CarouselBlock } from '@/shared/ui/index.js';

import {
    CollectionCreatedPlaylistsModel,
    CollectionCreatedPlaylistsDataItemModel
} from '../../model/blocks/CollectionCreatedPlaylists.js';

interface Props extends CollectionCreatedPlaylistsModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

const CollectionCreatedPlaylistsComponent = ({
    containerClassName,
    meta,
    data,
    forwardRef: ref
}: Props): ReactElement => {
    return (
        <CarouselBlock
            containerClassName={containerClassName}
            ref={ref}
            isLoading={!data?.items || data?.items?.length === 0}
            title={meta.title}
            description={meta.description}
            viewAllActionLink={meta.viewAllActionLink}
        >
            {data?.items.map((item: CollectionCreatedPlaylistsDataItemModel) => {
                return <PlaylistCard key={item.key} playlist={item} contentLinesCount={3} />;
            })}
        </CarouselBlock>
    );
};

export const CollectionCreatedPlaylists = forwardRef<HTMLDivElement, Props>(
    (props: Props, ref: Ref<HTMLDivElement>) => <CollectionCreatedPlaylistsComponent forwardRef={ref} {...props} />
);
