import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType, LandingEntityType } from '@yandex-music-int/resource-collection';

import { PlaylistWithArtists } from '@/features/playlist/index.js';

import { LandingBaseBlock } from './LandingBaseBlock.js';

export const PersonalPlaylistsDataItemData = types.model('PersonalPlaylistsDataItem', {
    playlist: PlaylistWithArtists,
    playlistType: types.string,
    description: types.maybe(types.string)
});

export const PersonalPlaylistsDataItem = types.model('PersonalPlaylistsDataItem', {
    type: types.literal(LandingEntityType.PERSONAL_PLAYLIST),
    data: PersonalPlaylistsDataItemData
});

export const PersonalPlaylistsData = types.model('PersonalPlaylistsData', {
    items: types.array(PersonalPlaylistsDataItem)
});

export const PersonalPlaylists = LandingBaseBlock.props({
    type: types.union(
        types.literal(LandingBlockType.PERSONAL_PLAYLISTS),
        types.literal(LandingBlockType.REWIND_PLAYLISTS)
    ),
    data: types.maybe(PersonalPlaylistsData)
})
    .named('PersonalPlaylists')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }

            return self.data?.items?.length !== 0;
        }
    }));

export type PersonalPlaylistsModel = Instance<typeof PersonalPlaylists>;
export type PersonalPlaylistsDataModel = Instance<typeof PersonalPlaylistsData>;
export type PersonalPlaylistsDataItemModel = Instance<typeof PersonalPlaylistsDataItem>;
export type PersonalPlaylistsDataItemDataModel = Instance<typeof PersonalPlaylistsDataItemData>;
