import { cast } from 'mobx-state-tree';

import { LandingAlbumEntity, LandingArtistEntity } from '@yandex-music-int/resource-collection';

import { AlbumWithArtistsModel } from '@/features/album/index.js';

import { hexToHsl } from '@/shared/lib/index.js';

import { landingArtistResponseToModel } from './landingArtistResponseToModel.js';

const DEFAULT_LIGHTNESS = 20;

export const landingAlbumResponseToModel = (
    album: LandingAlbumEntity,
    artists?: LandingArtistEntity[]
): AlbumWithArtistsModel => {
    let hslAverageColor = '';
    if (album.cover?.color) {
        const { h, s } = hexToHsl(album.cover?.color);
        hslAverageColor = `hsl(${h}, ${s}%, ${DEFAULT_LIGHTNESS}%)`;
    }

    return cast({
        id: album.id,
        title: album.title,
        coverUri: album.cover?.uri,
        type: album.albumType,
        contentWarning: album.contentWarning,
        artists: artists?.map(landingArtistResponseToModel),
        averageColor: album.cover?.color,
        hslAverageColor,
        available: album.available
    });
};
