import { ReactElement, forwardRef, Ref } from 'react';

import { LandingEntityType } from '@yandex-music-int/resource-collection';

import { AlbumCard } from '@/features/album/index.js';
import { PlaylistCard } from '@/features/playlist/index.js';

import { CarouselBlock } from '@/shared/ui/index.js';

import { NewPlaylistsDataItemModel, NewPlaylistsModel } from '../../model/blocks/NewPlaylists.js';

interface Props extends NewPlaylistsModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

const NewPlaylistsComponent = ({ containerClassName, meta, data, forwardRef: ref }: Props): ReactElement => {
    return (
        <CarouselBlock
            containerClassName={containerClassName}
            ref={ref}
            isLoading={!data?.items || data?.items?.length === 0}
            title={meta.title}
            description={meta.description}
            viewAllActionLink={meta.viewAllActionLink}
        >
            {data?.items.map((item: NewPlaylistsDataItemModel) => {
                switch (item.type) {
                    case LandingEntityType.LIKED_PLAYLIST:
                        return <PlaylistCard key={item.data.key} playlist={item.data} contentLinesCount={3} />;

                    case LandingEntityType.ALBUM:
                        return <AlbumCard key={item.data.id} album={item.data} contentLinesCount={3} />;
                }
            })}
        </CarouselBlock>
    );
};

export const NewPlaylists = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <NewPlaylistsComponent forwardRef={ref} {...props} />
));
