import { cast } from 'mobx-state-tree';

import { InStyleItem, InStyleResponse, InStyleDataItem } from '@yandex-music-int/resource-collection';

import { InStyleDataItemModel, InStyleDataModel } from '../../model/blocks/InStyle.js';

import { landingAlbumResponseToModel } from './common/landingAlbumResponseToModel.js';

export const inStyleResponseToModel = (response: InStyleResponse): InStyleDataModel => {
    const items = (response || {}).inStyleTabs?.map((item: InStyleItem, index: number): InStyleDataItemModel => {
        const data = item?.items.map(({ album, artists }: InStyleDataItem) =>
            landingAlbumResponseToModel(album, artists)
        );
        const covers = (item?.cover?.uri && [item?.cover.uri]) || [];

        return {
            tab: {
                id: index,
                title: item.title,
                covers: cast(covers)
            },
            data: cast(data)
        };
    });
    return cast({
        items
    });
};
