import { cast } from 'mobx-state-tree';

import { CollectionCreatedPlaylistsResponse, LandingLikedPlaylistItem } from '@yandex-music-int/resource-collection';

import { CollectionCreatedPlaylistsDataModel } from '../../model/blocks/CollectionCreatedPlaylists.js';

import { landingPlaylistResponseToModel } from './common/landingPlaylistResponseToModel.js';

export const collectionPlaylistCreatedResponseToModel = (
    response: CollectionCreatedPlaylistsResponse
): CollectionCreatedPlaylistsDataModel => {
    const items = (response || {}).createdPlaylists?.map((item: LandingLikedPlaylistItem) => {
        return landingPlaylistResponseToModel(item.data.playlist, item.data.likesCount);
    });

    return cast({
        items
    });
};
