import { cast } from 'mobx-state-tree';

import { CollectionLikedPlaylistsResponse, LandingLikedPlaylistItem } from '@yandex-music-int/resource-collection';

import { CollectionLikedPlaylistsDataModel } from '../../model/blocks/CollectionLikedPlaylists.js';

import { landingPlaylistResponseToModel } from './common/landingPlaylistResponseToModel.js';

export const collectionPlaylistLikedResponseToModel = (
    response: CollectionLikedPlaylistsResponse
): CollectionLikedPlaylistsDataModel => {
    const items = (response || {}).likedPlaylists?.map((item: LandingLikedPlaylistItem) => {
        return landingPlaylistResponseToModel(item.data.playlist, item.data.likesCount);
    });

    return cast({
        items
    });
};
