'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement } from 'react';

import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';

import { WithClassName } from '@/shared/lib/index.js';

import styles from './Wizard.module.scss';

interface WizardHoneycombCellProps {
    active?: boolean;
    coverUri?: string;
}

interface WizardHoneycombLineProps {
    position: 'middle' | 'top' | 'bottom';
    coverUri?: string;
}

interface Props extends WithClassName {
    coverUri?: string;
}

const WizardHoneycombCell = ({ active, coverUri }: WizardHoneycombCellProps): ReactElement => {
    const iconVariant = active ? 'liked' : 'like';
    const content = coverUri ? (
        <Image src={coverUri} size={100} fit="contain" withAvatarReplace />
    ) : (
        <Icon
            size="m"
            variant={iconVariant}
            className={clsx(styles.honeycombCellIcon, { [styles.honeycombCellIcon_active]: active })}
        />
    );

    return (
        <Paper radius="round" className={clsx([styles.honeycombCell, { [styles.honeycombCell_active]: active }])}>
            {content}
        </Paper>
    );
};

const WizardHoneycombLine = ({ position, coverUri }: WizardHoneycombLineProps): ReactElement => {
    return (
        <div className={clsx(styles.honeycombLine, styles[`honeycomb_${position}`])}>
            <WizardHoneycombCell />
            <WizardHoneycombCell />
            {position === 'middle' && <WizardHoneycombCell coverUri={coverUri} active />}
            <WizardHoneycombCell />
            <WizardHoneycombCell />
        </div>
    );
};

export const WizardHoneycomb = observer(({ className, coverUri }: Props): ReactElement => {
    return (
        <div className={clsx(styles.honeycomb, className)}>
            <WizardHoneycombLine position="top" />
            <WizardHoneycombLine position="middle" coverUri={coverUri} />
            <WizardHoneycombLine position="bottom" />
        </div>
    );
});
