import { forwardRef, Ref } from 'react';

import { Wizard as WizardComponent } from '@/entities/wizard/index.js';

import { WithClassName } from '@/shared/lib/index.js';

interface Props extends WithClassName {
    forwardRef?: Ref<HTMLDivElement>;
}

export const Wizard = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <WizardComponent forwardRef={ref} {...props} />
));
