import { cast } from 'mobx-state-tree';

import {
    LandingBlockType,
    LandingTabsBlock,
    LandingBlock,
    LandingTab,
    LandingTabsBlockData,
    LandingBlockData
} from '@yandex-music-int/resource-collection';

import { LoadingState } from '@/shared/lib/index.js';

import { LandingBlockMetaModel } from '../../model/blocks/LandingBaseBlock.js';
import { TabModel, TabsModel } from '../../model/blocks/Tabs.js';
import { LandingBlockModel } from '../../model/LandingBlock.js';
import { isLandingBlockExists } from '../isLandingBlockExists.js';

interface DisabledParts {
    disabledTabs?: string[];
    disabledBlocks?: string[];
}

interface ResponseToModelParams extends DisabledParts {
    blocks: LandingTabsBlock[];
}

interface TransforTabsParams extends DisabledParts {
    data: LandingTabsBlockData;
}

// Блоки, для которых не нужно делать дополнительный запрос в бэкенд для отображения
// Отображаются в скелетоне сразу, без шиммеров
const NO_FETCH_BLOCKS = [LandingBlockType.WIZARD];

const isValidBlock = (block: LandingBlock): boolean => {
    return Boolean(block?.data?.source?.uri) || NO_FETCH_BLOCKS.includes(block.type);
};

const transformData = (data: LandingBlockData): LandingBlockMetaModel => {
    return cast({
        title: data.title,
        description: data.description,
        source: data.source,
        viewAllActionLink: data.viewAllAction?.weblink
    });
};

const transformBlock = (block: LandingBlock): LandingBlockModel => {
    return {
        loadingState: LoadingState.IDLE,
        id: block.id,
        type: block.type,
        meta: transformData(block.data),
        data: undefined,
        isNeededToLoad: true,
        isLoading: true,
        isRejected: false,
        isVisible: true
    };
};

const transformTab =
    ({ disabledBlocks }: DisabledParts) =>
    (tab: LandingTab): TabModel => {
        return cast({
            meta: {
                id: tab.id,
                title: tab.title
            },
            blocks: tab.blocks
                .filter((block: LandingBlock) => {
                    return (
                        isLandingBlockExists(block.type) && isValidBlock(block) && !disabledBlocks?.includes(block.id)
                    );
                })
                .map(transformBlock)
        });
    };

const transformTabs = ({ data, disabledTabs, disabledBlocks }: TransforTabsParams): TabsModel => {
    return cast({
        loadingState: LoadingState.IDLE,
        isLoading: true,
        meta: {
            selectedTabIndex: data.selectedTabIndex,
            source: data.source
        },
        tabs: {
            loadingState: LoadingState.IDLE,
            data: data.tabs
                .filter((tab: LandingTab) => {
                    return !disabledTabs?.includes(tab.id);
                })
                .map(transformTab({ disabledBlocks }))
        }
    });
};

export const skeletonResponseToModel = ({
    blocks,
    disabledTabs,
    disabledBlocks
}: ResponseToModelParams): TabsModel | null => {
    const tabs = blocks?.find((block: LandingTabsBlock) => block.type === LandingBlockType.TABS);

    if (tabs) {
        return transformTabs({ data: tabs.data, disabledTabs, disabledBlocks });
    }

    return null;
};
