import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType } from '@yandex-music-int/resource-collection';

import { TrackWithArtists, TrackWithArtistsModel } from '@/features/track/index.js';

import { Playlist } from '@/entities/playlist/index.js';

import { LandingBaseBlock } from './LandingBaseBlock.js';

const PlaylistWithTracksData = types.model('PlaylistWithTracksData', {
    totalItemsCount: types.maybe(types.number),
    items: types.array(TrackWithArtists),
    playlist: Playlist
});

export const PlaylistWithTracks = LandingBaseBlock.props({
    type: types.union(
        types.literal(LandingBlockType.OPEN_PLAYLIST),
        types.literal(LandingBlockType.SMART_OPEN_PLAYLIST),
        types.literal(LandingBlockType.COLLECTION_PLAYLIST_WITH_LIKES)
    ),
    data: types.maybe(PlaylistWithTracksData)
})
    .named('PlaylistWithTracks')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }

            return self.data?.items?.length !== 0;
        }
    }));

export type PlaylistWithTracksModel = Instance<typeof PlaylistWithTracks>;
export type PlaylistWithTracksDataModel = Instance<typeof PlaylistWithTracksData>;
export type PlaylistWithTracksDataItemModel = TrackWithArtistsModel;
