import { Instance, cast, types } from 'mobx-state-tree';

import { LoadingState } from '@/shared/lib/index.js';

import { LandingBlock } from '../LandingBlock.js';

import { LandingBlockMetaSource } from './LandingBaseBlock.js';

const TabData = types.model('TabData', {
    id: types.string,
    title: types.string,
    subtitle: types.string,
    covers: types.array(types.string)
});

const Tab = types.model('Tab', {
    meta: types.model({
        id: types.string,
        title: types.string
    }),
    data: types.maybe(TabData),
    blocks: types.array(LandingBlock)
});

const TabsData = types
    .model('TabsData', {
        loadingState: types.enumeration(Object.values(LoadingState)),
        data: types.array(Tab)
    })
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            reset() {
                self.loadingState = LoadingState.IDLE;
                self.data = cast([]);
            }
        };
    });

export const Tabs = types
    .model('Tabs', {
        loadingState: types.enumeration(Object.values(LoadingState)),
        meta: types.maybeNull(
            types.model({
                selectedTabIndex: types.number,
                source: LandingBlockMetaSource
            })
        ),
        tabs: TabsData
    })
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isEmpty() {
            return self.tabs.data?.length === 0;
        }
    }))
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isLoading() {
            return self.isEmpty;
        },

        get isLoadedAndEmpty() {
            return [LoadingState.RESOLVE, LoadingState.REJECT].includes(self.loadingState) && self.isEmpty;
        }
    }));

export type TabDataModel = Instance<typeof TabData>;
export type TabModel = Instance<typeof Tab>;
export type TabsDataModel = Instance<typeof TabsData>;
export type TabsModel = Instance<typeof Tabs>;
