'use client';

import { observer } from 'mobx-react-lite';
import { forwardRef, Ref, ReactNode } from 'react';
import { useIntl } from 'react-intl';

import { YaMusicContextData, YaMusicContexts } from '@yandex-music-int/player-contexts';
import { MusicEntityData } from '@yandex-music-int/player-entities';
import { PlayContextParams } from '@yandex-music-int/sonata';

import { TrackPlaylist } from '@/features/track/index.js';

import { hasDuplicatesBefore } from '@/entities/playlist/index.js';

import { TrackVariant, useCreateFrom } from '@/shared/lib/index.js';
import { TracksBlock } from '@/shared/ui/index.js';

import { PlaylistWithTracksModel, PlaylistWithTracksDataItemModel } from '../../model/blocks/PlaylistWithTracks.js';

interface Props extends PlaylistWithTracksModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

export const PlaylistWithTracksComponent = observer(
    ({ containerClassName, meta, data, forwardRef: ref }: Props): ReactNode => {
        const from = useCreateFrom();
        const { formatMessage } = useIntl();
        const description = data?.totalItemsCount
            ? formatMessage({ id: 'entity-names.number-of-tracks' }, { counter: data?.totalItemsCount })
            : meta.description;

        const playContextParams = (id: string): PlayContextParams<YaMusicContextData, MusicEntityData> => {
            if (data) {
                return {
                    contextData: {
                        type: YaMusicContexts.Playlist,
                        meta: {
                            id: data.playlist.uuid
                        },
                        from
                    },
                    queueParams: {
                        index: data?.items.findIndex((item: PlaylistWithTracksDataItemModel) => item.id === id)
                    },
                    loadContextMeta: true
                };
            }

            return {
                contextData: {
                    type: YaMusicContexts.Playlist,
                    meta: {
                        id: ''
                    },
                    from
                }
            };
        };

        return (
            <TracksBlock
                containerClassName={containerClassName}
                isLoading={!data?.items || data?.items?.length === 0}
                title={meta.title}
                coverUrl={data?.playlist.coverUri}
                description={description}
                variant={TrackVariant.PLAYLIST}
                viewAllActionLink={data?.playlist.url}
                ref={ref}
            >
                {data?.items.map((item: PlaylistWithTracksDataItemModel, index: number) => {
                    return (
                        <TrackPlaylist
                            key={item.id}
                            track={item}
                            playContextParams={playContextParams(item.id)}
                            hasDuplicatesBefore={hasDuplicatesBefore(data?.items, item, index)}
                        />
                    );
                })}
            </TracksBlock>
        );
    }
);

export const PlaylistWithTracks = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <PlaylistWithTracksComponent forwardRef={ref} {...props} />
));
