import { hexToRgb } from '@/shared/lib/index.js';

const HEX_REGEXP = /^#[a-fA-F\d]{2}[a-fA-F\d]{2}[a-fA-F\d]{2}$/i;

const DEFAULT_COLOR = '#000000';

export const getFade = (color: string | undefined): string => {
    if (!color || !HEX_REGEXP.test(color)) {
        color = DEFAULT_COLOR;
    }

    const { r, g, b } = hexToRgb(color);

    return `linear-gradient(180.14deg, rgba(${r}, ${g}, ${b}, 0) 36.88%, rgba(${r}, ${g}, ${b}, 0.5) 64.8%, rgba(${r}, ${g}, ${b}, 0.9) 99.88%)`;
};
