import { cast } from 'mobx-state-tree';

import { LandingArtistEntity } from '@yandex-music-int/resource-collection';

import { ArtistModel } from '@/entities/artist/index.js';

export const landingArtistResponseToModel = (artist: LandingArtistEntity): ArtistModel => {
    return cast({
        id: String(artist.id),
        name: artist.name,
        coverUri: artist.cover?.uri
    });
};
