import { Instance, flow, getEnv, cast, isAlive, getRoot } from 'mobx-state-tree';

import {
    LandingBlockType,
    LandingSkeletonType,
    CollectionAlbumsResponse,
    CollectionArtistsResponse,
    CollectionCreatedPlaylistsResponse,
    CollectionLikedPlaylistsResponse,
    CollectionPlaylistWithLikesResponse,
    LandingBlockSource,
    LandingSkeletonResponse,
    NewPlaylistsResponse,
    NewReleasesResponse,
    RecentlyPlayedResponse,
    TabsResponse,
    ChartTracksResponse,
    InStyleResponse,
    EditorialArtistsResponse,
    LandingResourceParams,
    OpenPlaylistResponse,
    PromotionsResponse,
    PersonalPlaylistsResponse,
    SpecialResponse
} from '@yandex-music-int/resource-collection';

import { LoadingState, StoreEnv, Experiments } from '@/shared/lib/index.js';

import { chartTracksResponseToModel } from '../lib/mappers/chartTracksResponseToModel.js';
import { collectionAlbumsResponseToModel } from '../lib/mappers/collectionAlbumsResponseToModel.js';
import { collectionArtistsResponseToModel } from '../lib/mappers/collectionArtistsResponseToModel.js';
import { collectionPlaylistCreatedResponseToModel } from '../lib/mappers/collectionPlaylistCreatedResponseToModel.js';
import { collectionPlaylistLikedResponseToModel } from '../lib/mappers/collectionPlaylistLikedResponseToModel.js';
import { collectionPlaylistWithLikesResponseToModel } from '../lib/mappers/collectionPlaylistWithLikesResponseToModel.js';
import { editorialArtistsResponseToModel } from '../lib/mappers/editorialArtistsResponseToModel.js';
import { inStyleResponseToModel } from '../lib/mappers/inStyleResponseToModel.js';
import { newPlaylistsResponseToModel } from '../lib/mappers/newPlaylistsResponseToModel.js';
import { newReleasesResponseToModel } from '../lib/mappers/newReleasesResponseToModel.js';
import { openPlaylistResponseToModel } from '../lib/mappers/openPlaylistResponseToModel.js';
import { personalPlaylistsResponseToModel } from '../lib/mappers/personalPlaylistsResponseToModel.js';
import { promotionsResponseToModel } from '../lib/mappers/promotionsResponseToModel.js';
import { recentlyPlayedResponseToModel } from '../lib/mappers/recentlyPlayedResponseToModel.js';
import { skeletonResponseToModel } from '../lib/mappers/skeletonResponseToModel.js';
import { specialResponseToModel } from '../lib/mappers/specialResponseToModel.js';
import { tabsResponseToModel } from '../lib/mappers/tabResponseToModel.js';

import { LandingBlockMetaSourceModel } from './blocks/LandingBaseBlock.js';
import { TabDataModel, TabModel, Tabs } from './blocks/Tabs.js';
import { LandingBlockModel } from './LandingBlock.js';

type BlockResponse =
    | ChartTracksResponse
    | CollectionAlbumsResponse
    | CollectionArtistsResponse
    | CollectionCreatedPlaylistsResponse
    | CollectionLikedPlaylistsResponse
    | CollectionPlaylistWithLikesResponse
    | LandingBlockSource
    | NewPlaylistsResponse
    | NewReleasesResponse
    | RecentlyPlayedResponse
    | TabsResponse
    | InStyleResponse
    | EditorialArtistsResponse
    | OpenPlaylistResponse
    | PromotionsResponse
    | PersonalPlaylistsResponse
    | SpecialResponse;

export const Landing = Tabs
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getTabData: flow(function* (
                source: LandingBlockSource
            ): Generator<Promise<TabsResponse>, void, TabsResponse> {
                const { landingResource, logger } = getEnv<StoreEnv>(self);

                try {
                    self.tabs.loadingState = LoadingState.PENDING;

                    const response = yield landingResource.getBlock<TabsResponse>(source);
                    const tabs = tabsResponseToModel(response) || [];

                    self.tabs.data.forEach((tab: TabModel, index: number) => {
                        // TODO: Решение для того, чтобы убрать рассинхрон скелетона и ответа блока
                        const data = tabs.find((t: TabDataModel) => t.title === tab.meta.title);

                        tab.data = data || tabs[index];
                    });

                    self.tabs.loadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    self.loadingState = LoadingState.REJECT;
                }
            }),

            getTabBlock: flow(function* (
                tabIndex: number,
                id: string,
                source?: LandingBlockMetaSourceModel
            ): Generator<Promise<BlockResponse>, void, BlockResponse> {
                if (!source) {
                    return;
                }

                const block = self.tabs.data?.[tabIndex]?.blocks.find(
                    (tabBlock: LandingBlockModel) => tabBlock.id === id
                );

                if (!block) {
                    return;
                }

                const { landingResource, logger } = getEnv<StoreEnv>(self);

                try {
                    block.loadingState = LoadingState.PENDING;

                    const response = yield landingResource.getBlock<BlockResponse>(source, block.type);

                    switch (block.type) {
                        case LandingBlockType.CHART_TRACKS:
                            block.data = chartTracksResponseToModel(response as ChartTracksResponse);
                            break;

                        case LandingBlockType.COLLECTION_ALBUMS:
                            block.data = collectionAlbumsResponseToModel(response as CollectionAlbumsResponse);
                            break;

                        case LandingBlockType.COLLECTION_ARTISTS:
                            block.data = collectionArtistsResponseToModel(response as CollectionArtistsResponse);
                            break;

                        case LandingBlockType.PERSONAL_ARTISTS:
                        case LandingBlockType.NEW_STARS_ARTISTS:
                        case LandingBlockType.EDITORIAL_ARTISTS:
                            block.data = editorialArtistsResponseToModel(response as EditorialArtistsResponse);

                            break;

                        case LandingBlockType.COLLECTION_PLAYLISTS_CREATED:
                            block.data = collectionPlaylistCreatedResponseToModel(
                                response as CollectionCreatedPlaylistsResponse
                            );
                            break;

                        case LandingBlockType.COLLECTION_PLAYLISTS_LIKED:
                            block.data = collectionPlaylistLikedResponseToModel(
                                response as CollectionLikedPlaylistsResponse
                            );
                            break;

                        case LandingBlockType.OPEN_PLAYLIST:
                        case LandingBlockType.SMART_OPEN_PLAYLIST:
                            block.data = openPlaylistResponseToModel(response as OpenPlaylistResponse);
                            break;

                        case LandingBlockType.COLLECTION_PLAYLIST_WITH_LIKES:
                            block.data = collectionPlaylistWithLikesResponseToModel(
                                response as CollectionPlaylistWithLikesResponse
                            );
                            break;

                        case LandingBlockType.NEW_RELEASES:
                        case LandingBlockType.EDITORIAL_NEW_RELEASES:
                            block.data = newReleasesResponseToModel(response as NewReleasesResponse);
                            break;

                        case LandingBlockType.NEW_PLAYLISTS:
                        case LandingBlockType.EDITORIAL_COMPILATION:
                            block.data = newPlaylistsResponseToModel(response as NewPlaylistsResponse);
                            break;

                        case LandingBlockType.RECENTLY_PLAYED:
                            block.data = recentlyPlayedResponseToModel(response as RecentlyPlayedResponse);
                            break;

                        case LandingBlockType.IN_STYLE:
                            block.data = inStyleResponseToModel(response as InStyleResponse);
                            break;

                        case LandingBlockType.PROMOTIONS:
                        case LandingBlockType.EDITORIAL_PROMOTIONS:
                            block.data = promotionsResponseToModel(response as PromotionsResponse);
                            break;

                        case LandingBlockType.REWIND_PLAYLISTS:
                        case LandingBlockType.PERSONAL_PLAYLISTS:
                            block.data = personalPlaylistsResponseToModel(response as PersonalPlaylistsResponse);
                            break;

                        case LandingBlockType.SPECIAL:
                            block.data = specialResponseToModel(response as SpecialResponse);
                            break;
                    }

                    block.loadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    if (isAlive(block)) {
                        block.loadingState = LoadingState.REJECT;
                    }
                }
            })
        };
    })
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            // TODO: Сейчас все блоки загружается вместе. В будущем перейти на динамическую подзгрузку блоков (скелетон + 2 блока)
            async prefetchData(preloadBlocks?: number): Promise<void> {
                const requests = [];

                if (self.meta?.source) {
                    requests.push(self.getTabData(self.meta.source));
                }

                self.tabs.data.forEach((tab: TabModel, index: number) => {
                    tab.blocks.slice(0, preloadBlocks).forEach((block: LandingBlockModel) => {
                        requests.push(self.getTabBlock(index, block.id, block.meta.source));
                    });
                });

                if (requests.length > 0) {
                    await Promise.allSettled(requests);
                }
            }
        };
    })
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getSkeleton: flow(function* (
                params: LandingResourceParams,
                options?: {
                    preloadBlocks: number;
                }
            ): Generator<Promise<LandingSkeletonResponse | void>, void, LandingSkeletonResponse> {
                const { landingResource, logger } = getEnv<StoreEnv>(self);
                // @ts-ignore
                const { experiments } = getRoot(self);
                const disabledTabsExperiment = experiments.getExperiment(Experiments.WebNextDisableSkeletonTabs);
                const disabledBlocksExperiment = experiments.getExperiment(Experiments.WebNextDisableSkeletonBlocks);
                const isLandingDisabled = experiments.checkExperiment(Experiments.WebNextDisableLanding, 'on');
                // Отключаем только для главной, т.к для коллекции есть отдельный эксперимент
                const isSkeletonDisabled = isLandingDisabled && params.id === LandingSkeletonType.WEB_MAIN;
                if (isSkeletonDisabled || self.loadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.loadingState = LoadingState.PENDING;

                    const response = yield landingResource.getSkeleton(params);

                    const skeleton = skeletonResponseToModel({
                        blocks: response?.blocks,
                        disabledTabs: disabledTabsExperiment?.value?.tabIds,
                        disabledBlocks: disabledBlocksExperiment?.value?.blockIds
                    });

                    if (skeleton) {
                        self.meta = cast(skeleton.meta);
                        self.tabs.data = cast(skeleton.tabs.data);

                        yield self.prefetchData(options?.preloadBlocks);
                    }

                    // @ts-expect-error
                    // TS считает, что статусы никогда не пересекутся, но на самом деле мы можем уйти со страницы,
                    // раньше чем закончится последний ретрай и/или запрос, когда уже проставлен статус IDLE
                    if (self.loadingState !== LoadingState.IDLE) {
                        self.loadingState = LoadingState.RESOLVE;
                    }
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    if (self.loadingState !== LoadingState.IDLE) {
                        self.loadingState = LoadingState.REJECT;
                    }
                }
            }),
            reset() {
                self.loadingState = LoadingState.IDLE;
                self.meta = null;
                self.tabs.reset();
            }
        };
    });

export type LandingModel = Instance<typeof Landing>;
