import { cast } from 'mobx-state-tree';

import {
    ChartTracksResponse,
    LandingEntityType,
    LandingPlaylistEntity,
    TrackChart
} from '@yandex-music-int/resource-collection';

import { chartTrackWithArtistsResponseToModel } from '@/features/track/index.js';

import { ChartTracksDataModel, ChartTracksItemModel } from '../../model/blocks/ChartTracks.js';

import { landingPlaylistResponseToModel } from './common/landingPlaylistResponseToModel.js';

export const chartTracksResponseToModel = (response: ChartTracksResponse): ChartTracksDataModel => {
    const items = (response || {}).chart?.tracks?.map(({ track, chart }: TrackChart): ChartTracksItemModel => {
        return {
            type: LandingEntityType.TRACK,
            data: chartTrackWithArtistsResponseToModel(track, chart)
        };
    });

    return cast({
        items,
        playlist: landingPlaylistResponseToModel(response?.chart as LandingPlaylistEntity)
    });
};
