import { cast } from 'mobx-state-tree';

import { OpenPlaylistResponse } from '@yandex-music-int/resource-collection';

import { PlaylistWithTracksDataModel } from '@/widgets/landing/model/blocks/PlaylistWithTracks.js';

import { trackWithArtistsResponseToModel } from '@/features/track/index.js';

import { playlistResponseToModel } from '@/entities/playlist/index.js';

export const openPlaylistResponseToModel = (response: OpenPlaylistResponse): PlaylistWithTracksDataModel => {
    const playlistResponse = response || {};

    if (playlistResponse.cover?.uri) {
        playlistResponse.playlist.cover.uri = playlistResponse.cover?.uri;
    }

    return cast({
        items: playlistResponse.tracks.map(trackWithArtistsResponseToModel),
        playlist: playlistResponseToModel(playlistResponse.playlist)
    });
};
