import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType } from '@yandex-music-int/resource-collection';

import { AlbumWithArtists } from '@/features/album/index.js';

import { LandingBaseBlock } from './LandingBaseBlock.js';

export const NewReleasesDataItem = types.model('NewReleasesDataItem', {
    album: AlbumWithArtists,
    releaseDate: types.maybe(types.string)
});

const NewReleasesData = types.model('NewReleasesData', {
    items: types.array(NewReleasesDataItem)
});

export const NewReleases = LandingBaseBlock.props({
    type: types.union(
        types.literal(LandingBlockType.NEW_RELEASES),
        types.literal(LandingBlockType.EDITORIAL_NEW_RELEASES)
    ),
    data: types.maybe(NewReleasesData)
})
    .named('NewReleases')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }

            return self.data?.items?.length !== 0;
        }
    }));

export type NewReleasesModel = Instance<typeof NewReleases>;
export type NewReleasesDataModel = Instance<typeof NewReleasesData>;
export type NewReleasesDataItemModel = Instance<typeof NewReleasesDataItem>;
