'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useCallback, MouseEvent } from 'react';
import { useIntl } from 'react-intl';

import { YaMusicContexts } from '@yandex-music-int/player-contexts';
import { ContentWarning } from '@yandex-music-int/resource-collection';
import { createRipple } from '@yandex-music-int/sol/common/utils/createRipple.js';
import { ExplicitMark } from '@yandex-music-int/sol/components/ExplicitMark';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { getReleaseAlbumTitle } from '@/entities/album/index.js';

import {
    WithClassName,
    useNavigate,
    usePlayableContext,
    dateToDDMonthYYYYProps,
    buildUrl,
    useCreateFrom
} from '@/shared/lib/index.js';
import { Link, PlayButton } from '@/shared/ui/index.js';

import { NewReleasesDataItemModel } from '../../../../../model/blocks/NewReleases.js';

import styles from './NewReleaseCard.module.scss';

const DOUBLE_CLICK_EVENT = 2;

interface Props extends WithClassName, Pick<NewReleasesDataItemModel, 'album' | 'releaseDate'> {}

export const NewReleaseCard = observer(({ className, album, releaseDate }: Props): ReactElement => {
    const from = useCreateFrom();
    const navigate = useNavigate();
    const isExplicit = album.contentWarning === ContentWarning.EXPLICIT;
    const { formatMessage, formatDate } = useIntl();
    const entityName = formatMessage({ id: getReleaseAlbumTitle(album.type) });

    const handleCardClick = useCallback(
        (event: MouseEvent<HTMLDivElement>) => {
            createRipple(event, styles.ripple);

            if (event.detail === DOUBLE_CLICK_EVENT) {
                navigate(buildUrl('/album/:albumId', { albumId: album.id }));
            }
        },
        [navigate, album.id]
    );

    const { isPlaying, togglePlay } = usePlayableContext({
        playContextParams: {
            contextData: {
                type: YaMusicContexts.Album,
                meta: {
                    id: album.id
                },
                from
            },
            loadContextMeta: true
        }
    });

    return (
        <Paper radius="l" onClick={handleCardClick} className={clsx(styles.root, styles.card, className)}>
            <Link className={styles.coverLink} href={`/album/${album.id}`}>
                <Paper className={styles.cover} radius="s" withShadow>
                    <Image
                        className={styles.image}
                        src={album.coverUri}
                        alt={formatMessage({ id: 'entity-names.album-name' }, { albumName: album.title })}
                        size={100}
                        fit="cover"
                        withAvatarReplace
                    />
                </Paper>
            </Link>
            <div className={styles.description}>
                <Caption className={styles.type} variant="div" type="entity" size="s" weight="normal" lineClamp={1}>
                    {entityName}
                </Caption>
                <Link className={styles.titleLink} href={`/album/${album.id}`}>
                    <Caption
                        className={styles.title}
                        variant="div"
                        type="entity"
                        size="m"
                        weight="medium"
                        lineClamp={1}
                    >
                        {album.title}
                    </Caption>
                </Link>
                {releaseDate && (
                    <Caption className={styles.date} variant="div" type="entity" size="s" weight="medium" lineClamp={1}>
                        {formatDate(new Date(releaseDate), dateToDDMonthYYYYProps())}
                    </Caption>
                )}
            </div>
            <div className={styles.container}>
                {isExplicit && <ExplicitMark className={styles.explicitMark} />}
                <PlayButton
                    buttonVariant="default"
                    withHover={false}
                    className={styles.button}
                    iconClassName={styles.buttonIcon}
                    variant="filled"
                    iconSize="m"
                    isPlaying={isPlaying}
                    onClick={togglePlay}
                />
            </div>
        </Paper>
    );
});
