import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType, LandingEntityType } from '@yandex-music-int/resource-collection';

import { ChartTrackWithArtists } from '@/features/track/index.js';

import { Playlist } from '@/entities/playlist/index.js';

import { LandingBaseBlock } from './LandingBaseBlock.js';

export const ChartTracksItem = types.model('ChartTracksItem', {
    type: types.literal(LandingEntityType.TRACK),
    data: ChartTrackWithArtists
});

const ChartTracksData = types.model('ChartTracksData', {
    items: types.array(ChartTracksItem),
    playlist: Playlist
});

export const ChartTracks = LandingBaseBlock.props({
    type: types.literal(LandingBlockType.CHART_TRACKS),
    data: types.maybe(ChartTracksData)
})
    .named('ChartTracks')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }

            return self.data?.items?.length !== 0;
        }
    }));

export type ChartTracksModel = Instance<typeof ChartTracks>;
export type ChartTracksDataModel = Instance<typeof ChartTracksData>;
export type ChartTracksItemModel = Instance<typeof ChartTracksItem>;
