import { cast } from 'mobx-state-tree';

import { EditorialArtistsResponse, LandingArtistItem } from '@yandex-music-int/resource-collection';

import { CollectionArtistDataModel } from '../../model/blocks/CollectionArtists.js';

import { landingArtistResponseToModel } from './common/landingArtistResponseToModel.js';

export const editorialArtistsResponseToModel = (response: EditorialArtistsResponse): CollectionArtistDataModel => {
    const items = (response || {}).items?.map((artist: LandingArtistItem) => {
        return landingArtistResponseToModel(artist.data.artist);
    });

    return cast({
        items
    });
};
