import { cast } from 'mobx-state-tree';

import { SpecialResponse } from '@yandex-music-int/resource-collection';

import { SpecialDataModel } from '../../model/blocks/Special.js';

export const specialResponseToModel = (response?: SpecialResponse | null): SpecialDataModel => {
    return cast({
        title: response?.title,
        subtitle: response?.subtitle,
        buttonTitle: response?.buttonTitle,
        imageUrl: response?.imageUrl,
        align: response?.align,
        weblink: response?.action?.weblink
    });
};
