import { cast } from 'mobx-state-tree';

import { Promotion, PromotionsResponse } from '@yandex-music-int/resource-collection';

import { PromotionsDataItemModel, PromotionsDataModel } from '../../model/blocks/Promotions.js';

export const promotionsResponseToModel = (response: PromotionsResponse | null | undefined): PromotionsDataModel => {
    if (!Array.isArray(response?.promotions)) {
        return cast([]);
    }

    const items = response?.promotions.map((promotion: Promotion | null | undefined): PromotionsDataItemModel => {
        promotion = (promotion || {}) as Promotion;
        return {
            featureId: promotion.featureId,
            heading: promotion.heading,
            title: promotion.title,
            subtitle: promotion.subtitle,
            weblink: promotion.action?.weblink,
            imageUrl: promotion.imageUrl
        };
    });

    return cast({
        items
    });
};
