import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType, LandingEntityType } from '@yandex-music-int/resource-collection';

import { AlbumWithArtists } from '@/features/album/index.js';
import { PlaylistWithArtists } from '@/features/playlist/model/PlaylistWithArtists.js';

import { LandingBaseBlock } from './LandingBaseBlock.js';

export const NewPlaylistsDataPlaylistItem = types.model('NewPlaylistsDataPlaylistItem', {
    type: types.literal(LandingEntityType.LIKED_PLAYLIST),
    data: PlaylistWithArtists
});

export const NewPlaylistsDataAlbumItem = types.model('NewPlaylistsDataAlbumItem', {
    type: types.literal(LandingEntityType.ALBUM),
    data: AlbumWithArtists
});

export const NewPlaylistsDataItem = types.union(NewPlaylistsDataPlaylistItem, NewPlaylistsDataAlbumItem);

export const NewPlaylistsData = types.model('NewPlaylistsData', {
    items: types.array(NewPlaylistsDataItem)
});

export const NewPlaylists = LandingBaseBlock.props({
    type: types.union(
        types.literal(LandingBlockType.NEW_PLAYLISTS),
        types.literal(LandingBlockType.EDITORIAL_COMPILATION)
    ),
    data: types.maybe(NewPlaylistsData)
})
    .named('NewPlaylists')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }

            return self.data?.items?.length !== 0;
        }
    }));

export type NewPlaylistsModel = Instance<typeof NewPlaylists>;
export type NewPlaylistsDataModel = Instance<typeof NewPlaylistsData>;
export type NewPlaylistsDataItemModel = Instance<typeof NewPlaylistsDataItem>;
export type NewPlaylistsDataPlaylistItemModel = Instance<typeof NewPlaylistsDataPlaylistItem>;
export type NewPlaylistsDataAlbumItemModel = Instance<typeof NewPlaylistsDataAlbumItem>;
