import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType } from '@yandex-music-int/resource-collection';

import { AlbumWithArtists } from '@/features/album/index.js';

import { LandingBaseBlock } from './LandingBaseBlock.js';

export const InStyleDataItemTab = types.model('InStyleDataItemTab', {
    id: types.number,
    title: types.string,
    covers: types.array(types.string)
});

export const InStyleDataItem = types.model('InStyleDataItem', {
    tab: InStyleDataItemTab,
    data: types.array(AlbumWithArtists)
});

const InStyleData = types.model('InStyleData', {
    items: types.array(InStyleDataItem)
});

export const InStyle = LandingBaseBlock.props({
    type: types.literal(LandingBlockType.IN_STYLE),
    data: types.maybe(InStyleData)
})
    .named('InStyle')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }

            const isNoTabs = self.data?.items.length === 0;
            const isTabsEmpty = self.data?.items?.every((item: InStyleDataItemModel) => !item?.data);

            return !isNoTabs || !isTabsEmpty;
        }
    }));

export type InStyleModel = Instance<typeof InStyle>;
export type InStyleDataModel = Instance<typeof InStyleData>;
export type InStyleDataItemModel = Instance<typeof InStyleDataItem>;
export type InStyleDataItemTabModel = Instance<typeof InStyleDataItemTab>;
