import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType, SpecialAlign } from '@yandex-music-int/resource-collection';

import { LandingBaseBlock } from './LandingBaseBlock.js';

const SpecialData = types.model('SpecialData', {
    title: types.maybe(types.string),
    subtitle: types.maybe(types.string),
    buttonTitle: types.maybe(types.string),
    imageUrl: types.maybe(types.string),
    align: types.maybe(types.enumeration(Object.values(SpecialAlign))),
    weblink: types.maybeNull(types.string)
});

export const Special = LandingBaseBlock.props({
    type: types.literal(LandingBlockType.SPECIAL),
    data: types.maybe(SpecialData)
})
    .named('Special')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }
            return Boolean(self.data);
        }
    }));

export type SpecialModel = Instance<typeof Special>;
export type SpecialDataModel = Instance<typeof SpecialData>;
