import { cast } from 'mobx-state-tree';

import { CollectionAlbumsResponse, LandingAlbumItem } from '@yandex-music-int/resource-collection';

import { CollectionAlbumsDataModel } from '../../model/blocks/CollectionAlbums.js';

import { landingAlbumResponseToModel } from './common/landingAlbumResponseToModel.js';

export const collectionAlbumsResponseToModel = (response: CollectionAlbumsResponse): CollectionAlbumsDataModel => {
    const items = (response || {}).likedAlbums?.map((likedAlbums: LandingAlbumItem) => {
        return landingAlbumResponseToModel(likedAlbums.data.album, likedAlbums.data.artists);
    });

    return cast({
        items
    });
};
