import { cast } from 'mobx-state-tree';

import { LandingPlaylistEntity } from '@yandex-music-int/resource-collection';

import { PlaylistWithArtistsModel } from '@/features/playlist/index.js';

export const landingPlaylistResponseToModel = (
    playlist: LandingPlaylistEntity,
    likesCount?: number
): PlaylistWithArtistsModel => {
    return cast({
        available: true,
        uuid: playlist.playlistUuid,
        title: playlist.title,
        uid: playlist.uid,
        kind: playlist.kind,
        coverUri: playlist.cover?.uri,
        likesCount
    });
};
