import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType } from '@yandex-music-int/resource-collection';

import { PlaylistWithArtists, PlaylistWithArtistsModel } from '@/features/playlist/index.js';

import { LandingBaseBlock } from './LandingBaseBlock.js';

const CollectionLikedPlaylistsData = types.model('CollectionLikedPlaylistsData', {
    items: types.array(PlaylistWithArtists)
});

export const CollectionLikedPlaylists = LandingBaseBlock.props({
    type: types.literal(LandingBlockType.COLLECTION_PLAYLISTS_LIKED),
    data: types.maybe(CollectionLikedPlaylistsData)
})
    .named('CollectionLikedPlaylists')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }

            return self.data?.items?.length !== 0;
        }
    }));

export type CollectionLikedPlaylistsModel = Instance<typeof CollectionLikedPlaylists>;
export type CollectionLikedPlaylistsDataModel = Instance<typeof CollectionLikedPlaylistsData>;
export type CollectionLikedPlaylistsDataItemModel = PlaylistWithArtistsModel;
