import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType } from '@yandex-music-int/resource-collection';

import { LandingBaseBlock } from './LandingBaseBlock.js';

export const Wizard = LandingBaseBlock.props({
    type: types.literal(LandingBlockType.WIZARD),
    data: types.undefined
})
    .named('Wizard')
    .views(() => ({
        get isVisible() {
            return true;
        }
    }));

export type WizardModel = Instance<typeof Wizard>;
