import { observer } from 'mobx-react-lite';
import { forwardRef, Ref, ReactNode } from 'react';

import { YaMusicContextData, YaMusicContexts } from '@yandex-music-int/player-contexts';
import { MusicEntityData } from '@yandex-music-int/player-entities';
import { PlayContextParams } from '@yandex-music-int/sonata';

import { TrackChart } from '@/features/track/index.js';

import { useCreateFrom } from '@/shared/lib/index.js';
import { TracksBlock } from '@/shared/ui/index.js';

import { ChartTracksItemModel, ChartTracksModel } from '../../model/blocks/ChartTracks.js';

interface Props extends ChartTracksModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

export const ChartTracksComponent = observer(
    ({ containerClassName, meta, data, forwardRef: ref }: Props): ReactNode => {
        const from = useCreateFrom();
        const playContextParams = (id: string): PlayContextParams<YaMusicContextData, MusicEntityData> => {
            if (data) {
                return {
                    contextData: {
                        type: YaMusicContexts.Playlist,
                        meta: {
                            id: data.playlist.uuid
                        },
                        from
                    },
                    queueParams: {
                        index: data.items.findIndex((item: ChartTracksItemModel) => item.data.id === id)
                    },
                    loadContextMeta: true
                };
            }

            return {
                contextData: {
                    type: YaMusicContexts.Playlist,
                    meta: {
                        id: ''
                    },
                    from
                }
            };
        };

        return (
            <TracksBlock
                containerClassName={containerClassName}
                isLoading={!data?.items || data.items.length === 0}
                title={meta.title}
                description={meta.description}
                ref={ref}
                viewAllActionLink={meta.viewAllActionLink}
            >
                {data?.items.map((item: ChartTracksItemModel) => {
                    return (
                        <TrackChart
                            key={item.data.id}
                            track={item.data}
                            playContextParams={playContextParams(item.data.id)}
                        />
                    );
                })}
            </TracksBlock>
        );
    }
);

export const ChartTracks = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <ChartTracksComponent forwardRef={ref} {...props} />
));
