import { types, Instance } from 'mobx-state-tree';

import { LandingBlockType, LandingEntityType } from '@yandex-music-int/resource-collection';

import { AlbumWithArtists } from '@/features/album/index.js';
import { PlaylistWithArtists } from '@/features/playlist/model/PlaylistWithArtists.js';

import { Artist } from '@/entities/artist/index.js';

import { LandingBaseBlock } from './LandingBaseBlock.js';

export const RecentlyPlayedDataAlbumItem = types.model('RecentlyPlayedDataAlbumItem', {
    type: types.literal(LandingEntityType.ALBUM),
    data: AlbumWithArtists
});

export const RecentlyPlayedDataPlaylistItem = types.model('RecentlyPlayedDataPlaylistItem', {
    type: types.literal(LandingEntityType.PLAYLIST),
    data: PlaylistWithArtists
});

export const RecentlyPlayedDataArtistItem = types.model('RecentlyPlayedDataArtistItem', {
    type: types.literal(LandingEntityType.ARTIST),
    data: Artist
});

export const RecentlyPlayedDataItem = types.union(
    RecentlyPlayedDataAlbumItem,
    RecentlyPlayedDataPlaylistItem,
    RecentlyPlayedDataArtistItem
);

const RecentlyPlayedData = types.model('RecentlyPlayedData', {
    items: types.array(RecentlyPlayedDataItem)
});

export const RecentlyPlayed = LandingBaseBlock.props({
    type: types.literal(LandingBlockType.RECENTLY_PLAYED),
    data: types.maybe(RecentlyPlayedData)
})
    .named('RecentlyPlayed')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }

            return self.data?.items?.length !== 0;
        }
    }));

export type RecentlyPlayedDataAlbumItemModel = Instance<typeof RecentlyPlayedDataAlbumItem>;
export type RecentlyPlayedDataPlaylistItemModel = Instance<typeof RecentlyPlayedDataPlaylistItem>;
export type RecentlyPlayedDataArtistItemModel = Instance<typeof RecentlyPlayedDataArtistItem>;
export type RecentlyPlayedDataItemModel = Instance<typeof RecentlyPlayedDataItem>;
export type RecentlyPlayedDataModel = Instance<typeof RecentlyPlayedData>;
export type RecentlyPlayedModel = Instance<typeof RecentlyPlayed>;
