'use client';

import { ReactElement, useMemo, CSSProperties } from 'react';

import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';

import { SeparatedArtists } from '@/entities/artist/index.js';

import { Link } from '@/shared/ui/index.js';

import { getFade } from '../../../../lib/getFade.js';
import { NewReleasesDataItemModel } from '../../../../model/blocks/NewReleases.js';

import styles from './NewRelease.module.scss';
import { NewReleaseCard } from './NewReleaseCard/NewReleaseCard.js';

interface Props extends NewReleasesDataItemModel {}

export const NewRelease = ({ album, releaseDate }: Props): ReactElement => {
    const fistArtist = album?.artists?.[0];

    const style = useMemo(() => {
        return {
            '--new-releases-card-background': album?.hslAverageColor
        };
    }, [album?.hslAverageColor]);

    return (
        <div style={style as CSSProperties} className={styles.root}>
            <div className={styles.cover}>
                <Paper radius="round" withShadow>
                    <Image
                        className={styles.image}
                        src={fistArtist?.coverUri}
                        alt={fistArtist?.name}
                        size={300}
                        fit="cover"
                        withAvatarReplace
                    />
                    <div className={styles.fade} style={{ background: getFade(album.coverUri) }} />
                </Paper>

                <Link className={styles.fade} aria-label={fistArtist?.name} href={`/artist/${fistArtist?.id}`} />

                <SeparatedArtists
                    className={styles.artists}
                    lineClamp={2}
                    artists={album?.artists}
                    linkClassName={styles.artistLink}
                    captionClassName={styles.artistCaption}
                    variant="breakWord"
                />
            </div>

            <NewReleaseCard className={styles.card} album={album} releaseDate={releaseDate} />
        </div>
    );
};
