import { ReactElement, forwardRef, Ref } from 'react';

import { AlbumCard } from '@/features/album/index.js';

import { CarouselBlock } from '@/shared/ui/index.js';

import { CollectionAlbumsModel, CollectionAlbumsDataItemModel } from '../../model/blocks/CollectionAlbums.js';

interface Props extends CollectionAlbumsModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

const CollectionAlbumsComponent = ({ containerClassName, meta, data, forwardRef: ref }: Props): ReactElement => {
    return (
        <CarouselBlock
            containerClassName={containerClassName}
            ref={ref}
            isLoading={!data?.items || data?.items?.length === 0}
            title={meta.title}
            description={meta.description}
            viewAllActionLink={meta.viewAllActionLink}
        >
            {data?.items.map((item: CollectionAlbumsDataItemModel) => {
                return <AlbumCard key={item.id} album={item} contentLinesCount={3} />;
            })}
        </CarouselBlock>
    );
};

export const CollectionAlbums = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <CollectionAlbumsComponent forwardRef={ref} {...props} />
));
