'use client';

import { ReactElement, Ref, forwardRef, useId, useMemo, useRef } from 'react';

import { Carousel } from '@yandex-music-int/sol/components/Carousel';
import { Shimmer } from '@yandex-music-int/sol/components/Shimmer';

import { BlockHeader, CarouselControls } from '@/shared/ui/index.js';

import { NewReleasesModel, NewReleasesDataItemModel } from '../../../model/blocks/NewReleases.js';

import { NewRelease } from './NewRelease/NewRelease.js';
import styles from './NewReleases.module.scss';

interface Props extends NewReleasesModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

const CarouselShimmer = (): ReactElement[] => {
    const items = Array.from(Array(6).keys());

    return items.map(
        (item: number): ReactElement => (
            <div key={item}>
                <Shimmer radius="round" className={styles.shimmerImage} />
                <Shimmer radius="l" className={styles.shimmerCard} />
            </div>
        )
    );
};

const NewReleasesComponent = ({ containerClassName, meta, data, forwardRef: ref }: Props): ReactElement => {
    const id = useId();
    const carouselRef = useRef<HTMLOListElement | null>(null);

    const content = useMemo((): ReactElement[] | undefined => {
        if (!data?.items || data?.items?.length === 0) {
            return CarouselShimmer();
        }

        return data?.items.map((item: NewReleasesDataItemModel): ReactElement => {
            return <NewRelease key={item.album.id} {...item} />;
        });
    }, [data?.items]);

    return (
        <section ref={ref} className={styles.root}>
            <BlockHeader
                className={containerClassName}
                labeledForId={id}
                title={meta.title}
                viewAllActionLink={meta.viewAllActionLink}
                controls={<CarouselControls className={styles.controls} carouselRef={carouselRef} />}
            />
            <Carousel
                ref={carouselRef}
                itemClassName={styles.item}
                containerClassName={styles.content}
                aria-labelledby={id}
            >
                {content}
            </Carousel>
        </section>
    );
};

export const NewReleases = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>): ReactElement => {
    return <NewReleasesComponent forwardRef={ref} {...props} />;
});
