'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, createRef, ReactNode, useState, useEffect } from 'react';

import { useIntersectionObserver } from '@yandex-music-int/sol/common/hooks/useIntersectionObserver.js';
import { TabPanel, useTabsState, TabsState } from '@yandex-music-int/sol/components/Tabs';

import { AnalyticsBlockProvider } from '@/shared/lib/index.js';
import { TabCarousel, Tab, TabCarouselShimmerWithCovers } from '@/shared/ui/index.js';

import { TabModel } from '../../model/blocks/Tabs.js';
import { LandingModel } from '../../model/Landing.js';
import { LandingBlockModel } from '../../model/LandingBlock.js';

import styles from './Skeleton.module.scss';
import { skeletonBlockMap } from './SkeletonBlockMap.js';

interface Props {
    landing: LandingModel;

    errorComponent: ReactElement;
}

const Block = observer(
    ({
        landing,
        block,
        tabIndex
    }: {
        landing: LandingModel;
        block: LandingBlockModel;
        tabIndex: number;
    }): ReactNode => {
        const { id, type, meta, data, isNeededToLoad, isLoading, isRejected } = block;
        const Component = skeletonBlockMap[type];
        const ref = createRef<HTMLDivElement>();

        const [shouldLoadOnShow, setShouldLoadOnShow] = useState(isNeededToLoad);
        const isVisible = useIntersectionObserver(ref, !shouldLoadOnShow);

        useEffect(() => {
            if (isVisible && shouldLoadOnShow) {
                setShouldLoadOnShow(false);
                landing.getTabBlock(tabIndex, id, meta.source);
            }
        }, [isVisible, tabIndex, id, meta.source, landing, shouldLoadOnShow, meta.title]);

        return (
            <AnalyticsBlockProvider blockId={`discovery_block-${id}`}>
                <Component
                    containerClassName={styles.container}
                    ref={ref}
                    key={id}
                    // Нам нужно показывать шиммеры, если блок загружается или произошла ошибка
                    isLoading={isLoading || isRejected}
                    meta={meta}
                    data={data}
                />
            </AnalyticsBlockProvider>
        );
    }
);

const SingleTab = ({
    landing,
    tab,
    tabIndex
}: {
    landing: LandingModel;
    tab: TabModel;
    tabIndex: number;
}): ReactElement => {
    return (
        <div className={styles.root}>
            {tab.blocks
                .filter((block: LandingBlockModel) => block.isVisible)
                .map((block: LandingBlockModel) => {
                    return <Block key={block.id} landing={landing} block={block} tabIndex={tabIndex} />;
                })}
        </div>
    );
};

const MultipleTab = ({ landing, tabsState }: { landing: LandingModel; tabsState: TabsState }): ReactElement => {
    const { tabs } = landing;

    return (
        <>
            <TabCarousel
                className={clsx(styles.container, styles.tabCarousel)}
                isLoading={landing.isLoading}
                shimmer={<TabCarouselShimmerWithCovers className={styles.container} />}
                {...tabsState}
            >
                {tabs.data.map(({ meta, data }: TabModel, index: number) => {
                    return (
                        <Tab
                            className={styles.tab}
                            value={index}
                            key={meta.id}
                            aria-label={meta.title}
                            title={meta.title}
                            subtitle={data?.subtitle}
                            covers={data?.covers}
                            withCovers
                        />
                    );
                })}
            </TabCarousel>

            {tabs.data.map((tab: TabModel, tabIndex: number) => {
                return (
                    <TabPanel key={tab.meta.id} name={tabIndex} value={tabsState.value}>
                        {SingleTab({ landing, tab, tabIndex })}
                    </TabPanel>
                );
            })}
        </>
    );
};

export const Skeleton = observer(({ landing, errorComponent }: Props): ReactElement | null => {
    const tabsState = useTabsState(landing.meta?.selectedTabIndex ?? 0);
    const isMultipleTabs = landing.tabs.data?.length > 1;
    const firstTab = landing.tabs.data?.[0];

    if (isMultipleTabs) {
        return MultipleTab({ landing, tabsState });
    }

    if (firstTab) {
        return SingleTab({ landing, tab: firstTab, tabIndex: 0 });
    }

    if (landing.isLoadedAndEmpty) {
        return errorComponent;
    }

    return null;
});
