import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType } from '@yandex-music-int/resource-collection';

import { LandingBaseBlock } from './LandingBaseBlock.js';

export const PromotionsDataItem = types.model('PromotionsDataItem', {
    featureId: types.string,
    title: types.string,
    subtitle: types.string,
    heading: types.string,
    weblink: types.string,
    imageUrl: types.string
});

const PromotionsData = types.model('PromotionsData', {
    items: types.array(PromotionsDataItem)
});

export const Promotions = LandingBaseBlock.props({
    type: types.union(types.literal(LandingBlockType.PROMOTIONS), types.literal(LandingBlockType.EDITORIAL_PROMOTIONS)),
    data: types.maybe(PromotionsData)
})
    .named('Promotions')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }

            return self.data?.items?.length !== 0;
        }
    }));

export type PromotionsModel = Instance<typeof Promotions>;
export type PromotionsDataModel = Instance<typeof PromotionsData>;
export type PromotionsDataItemModel = Instance<typeof PromotionsDataItem>;
