import { ReactElement, forwardRef, Ref, useCallback } from 'react';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Caption, Heading } from '@yandex-music-int/sol/components/Typography';

import { useNavigate } from '@/shared/lib/index.js';

import { SpecialModel } from '../../../model/blocks/Special.js';

import styles from './Special.module.scss';

interface Props extends SpecialModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

const SpecialComponent = ({ containerClassName, meta, data, forwardRef: ref }: Props): ReactElement => {
    const navigate = useNavigate();

    const onClickHandler = useCallback(() => {
        if (data?.weblink) {
            navigate(data?.weblink);
        }
    }, [data?.weblink, navigate]);

    return (
        <section ref={ref} title={meta.title} className={containerClassName}>
            <div className={styles.root}>
                <div className={styles.actions}>
                    <div className={styles.textContainer}>
                        {Boolean(data?.title) && (
                            <Heading variant="h3" weight="black" size="xl" className={styles.title} lineClamp={2}>
                                {data?.title}
                            </Heading>
                        )}
                        {Boolean(data?.subtitle) && (
                            <Caption variant="span" type="text" size="l" weight="medium" lineClamp={2}>
                                {data?.subtitle}
                            </Caption>
                        )}
                    </div>
                    {Boolean(data?.weblink) && Boolean(data?.buttonTitle) && (
                        <Button
                            color="primary"
                            radius="xxxl"
                            className={styles.button}
                            role="link"
                            onClick={onClickHandler}
                        >
                            <Caption variant="span" type="controls" size="m" weight="medium">
                                {data?.buttonTitle}
                            </Caption>
                        </Button>
                    )}
                </div>
                <div className={styles.imageContainer}>
                    <Image
                        src={data?.imageUrl}
                        withAvatarReplace
                        fit="cover"
                        withFallback={false}
                        className={styles.image}
                    />
                </div>
            </div>
        </section>
    );
};

export const Special = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <SpecialComponent forwardRef={ref} {...props} />
));
