import { cast } from 'mobx-state-tree';

import {
    LandingEntityType,
    LandingPersonalPlaylistItem,
    PersonalPlaylistsResponse
} from '@yandex-music-int/resource-collection';

import { PersonalPlaylistsDataItemModel, PersonalPlaylistsDataModel } from '../../model/blocks/PersonalPlaylists.js';

import { landingPlaylistResponseToModel } from './common/landingPlaylistResponseToModel.js';

export const personalPlaylistsResponseToModel = (response?: PersonalPlaylistsResponse): PersonalPlaylistsDataModel => {
    if (!response || !Array.isArray(response.items)) {
        return cast([]);
    }

    const items = response.items.map(
        (item: LandingPersonalPlaylistItem): PersonalPlaylistsDataItemModel => ({
            type: LandingEntityType.PERSONAL_PLAYLIST,
            data: {
                playlist: landingPlaylistResponseToModel(item.data.playlist),
                playlistType: item.data.playlistType,
                description: item.data.description
            }
        })
    );

    return cast({
        items
    });
};
