import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType } from '@yandex-music-int/resource-collection';

import { AlbumWithArtists, AlbumWithArtistsModel } from '@/features/album/index.js';

import { LandingBaseBlock } from './LandingBaseBlock.js';

const CollectionAlbumsData = types.model('CollectionAlbumsData', {
    items: types.array(AlbumWithArtists)
});

export const CollectionAlbums = LandingBaseBlock.props({
    type: types.literal(LandingBlockType.COLLECTION_ALBUMS),
    data: types.maybe(CollectionAlbumsData)
})
    .named('CollectionAlbums')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }

            return self.data?.items?.length !== 0;
        }
    }));

export type CollectionAlbumsModel = Instance<typeof CollectionAlbums>;
export type CollectionAlbumsDataModel = Instance<typeof CollectionAlbumsData>;
export type CollectionAlbumsDataItemModel = AlbumWithArtistsModel;
