import { ReactElement, Ref, forwardRef, useId, useMemo, useRef } from 'react';

import { Carousel } from '@yandex-music-int/sol/components/Carousel';

import { PromotionsDataItemModel, PromotionsModel } from '@/widgets/landing/model/blocks/Promotions.js';

import { BlockHeader, CarouselControls } from '@/shared/ui/index.js';

import { PromotionCard } from './PromotionCard/PromotionsCard.jsx';
import styles from './Promotions.module.scss';
import { PromotionShimmer } from './PromotionShimmer/PromotionShimmer.jsx';

interface Props extends PromotionsModel {
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
}

const PromotionsComponentShimmer = (): ReactElement[] => {
    const items = Array.from(Array(6).keys());
    return items.map((index: number): ReactElement => {
        return <PromotionShimmer key={index} />;
    });
};

export const PromotionsComponent = ({ data, meta, containerClassName, forwardRef: ref }: Props): ReactElement => {
    const id = useId();
    const carouselRef = useRef<HTMLOListElement | null>(null);

    const content = useMemo((): ReactElement[] | undefined => {
        if (!data?.items || data?.items?.length === 0) {
            return PromotionsComponentShimmer();
        }

        return data?.items.map((item: PromotionsDataItemModel): ReactElement => {
            return <PromotionCard key={item.featureId} promotion={item} />;
        });
    }, [data?.items]);

    return (
        <section className={styles.root} ref={ref}>
            <BlockHeader
                className={containerClassName}
                labeledForId={id}
                title={meta.title}
                controls={<CarouselControls className={styles.controls} carouselRef={carouselRef} />}
            />
            <Carousel
                containerClassName={styles.content}
                ref={carouselRef}
                itemClassName={styles.item}
                aria-labelledby={id}
            >
                {content}
            </Carousel>
        </section>
    );
};

export const Promotions = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>): ReactElement => {
    return <PromotionsComponent forwardRef={ref} {...props} />;
});
