import { Instance, types } from 'mobx-state-tree';

import { LandingBlockType } from '@yandex-music-int/resource-collection';

import { PlaylistWithArtists, PlaylistWithArtistsModel } from '@/features/playlist/index.js';

import { LandingBaseBlock } from './LandingBaseBlock.js';

const CollectionCreatedPlaylistsData = types.model('CollectionPlaylistsData', {
    items: types.array(PlaylistWithArtists)
});

export const CollectionCreatedPlaylists = LandingBaseBlock.props({
    type: types.literal(LandingBlockType.COLLECTION_PLAYLISTS_CREATED),
    data: types.maybe(CollectionCreatedPlaylistsData)
})
    .named('CollectionCreatedPlaylists')
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isVisible() {
            if (self.isLoading || self.isRejected || self.isNeededToLoad) {
                return true;
            }

            return self.data?.items?.length !== 0;
        }
    }));

export type CollectionCreatedPlaylistsModel = Instance<typeof CollectionCreatedPlaylists>;
export type CollectionCreatedPlaylistsDataModel = Instance<typeof CollectionCreatedPlaylistsData>;
export type CollectionCreatedPlaylistsDataItemModel = PlaylistWithArtistsModel;
