import { cast } from 'mobx-state-tree';

import {
    LandingEntityType,
    LandingAlbumEntity,
    LandingPlaylistEntity,
    NewPlaylistsItem,
    NewPlaylistsResponse,
    LandingArtistEntity
} from '@yandex-music-int/resource-collection';

import {
    NewPlaylistsDataAlbumItemModel,
    NewPlaylistsDataModel,
    NewPlaylistsDataPlaylistItemModel
} from '../../model/blocks/NewPlaylists.js';

import { landingAlbumResponseToModel } from './common/landingAlbumResponseToModel.js';
import { landingPlaylistResponseToModel } from './common/landingPlaylistResponseToModel.js';

const albumResponseToModel = (
    album: LandingAlbumEntity,
    artists: LandingArtistEntity[]
): NewPlaylistsDataAlbumItemModel => {
    return {
        type: LandingEntityType.ALBUM,
        data: landingAlbumResponseToModel(album, artists)
    };
};

const playlistResponseToModel = (
    playlist: LandingPlaylistEntity,
    likesCount: number
): NewPlaylistsDataPlaylistItemModel => {
    return {
        type: LandingEntityType.LIKED_PLAYLIST,
        data: landingPlaylistResponseToModel(playlist, likesCount)
    };
};

export const newPlaylistsResponseToModel = (response: NewPlaylistsResponse): NewPlaylistsDataModel => {
    const items = (response || {}).items?.map((newPlaylist: NewPlaylistsItem) => {
        switch (newPlaylist.type) {
            case LandingEntityType.LIKED_PLAYLIST:
                return playlistResponseToModel(newPlaylist.data.playlist, newPlaylist.data.likesCount);

            case LandingEntityType.ALBUM:
                return albumResponseToModel(newPlaylist.data.album, newPlaylist.data.artists);
        }
    });

    return cast({
        items
    });
};
